
body{
    box-sizing: content-box;
    font-family: sans-serif;
    margin: 5%;
}


/*----- Single Institute------------ */
.header{
    align-items: center;
}

/* Ranking comp */
.info-section{
    margin-top: 35px;
}
.rankings{
    padding: 15px;
    background-color: aquamarine;
    border-radius: 10px;
    margin-left: 30px;
}
.rankings p{
    margin-left: 10px;
}
.rankings span, .test-overview span{
    background-color: white;
    padding: 10px;
    margin-left: 10px;
    display: inline-block;
    min-width: 50px;
}

/*  Test */
.test-overview{
    padding:20px;
    background-color: lightpink;
    margin-right: 35px;
    width: 200px;
}
.test-overview p{
    font-weight: bold;
}

/* Course info*/
.course-overview{
    background-color: whitesmoke;
    padding: 30px;
}
.course-overview div{
    justify-content: space-between;
}
.course-overview div p{
    color: royalblue;
    font-size: 30px;
}
.course-overview div p span{
    color: rgb(20, 20, 20);
    font-weight: bold;
    font-size: 20px;
}